<template>
  <div class="ratings" ref="ratings">
    <div class="rating-content">
      <div class="grade">
        <div class="comprehensive border-1px">
          <h1 class="score">{{seller.score}}</h1>
          <div class="text">综合评分</div>
          <p class="rankRate">高于周边商家{{seller.rankRate}}</p>
        </div>
        <div class="items">
          <div class="serviceScore">服务态度
            <div class="star-wrapper">
              <BaseStar :score="4.1" :size="36"></BaseStar>
            </div>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="foodScore">商品评分
            <div class="star-wrapper">
              <BaseStar :score="4.3" :size="36"></BaseStar>
            </div>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <p class="deliveryTime">
            送达时间<span class="time">{{seller.deliveryTime}}分钟</span>
          </p>
        </div>
      </div>
      <BaseSpilt></BaseSpilt>
      <SelectRatings :ratings="ratings" :desc="desc" :onlyContent="onlyContent" :selectType="selectType" @custom="select1" @toggle="toggleContent"></SelectRatings>
      <ul>
        <li v-for="(rating,index) in ratings" :key="index" class="rating-item border-1px" v-show="showRating(rating.rateType, rating.text)">
          <div class="user">
            <img :src="rating.avatar" class="avatar" width="28" height="28">
            <div class="name">
              <span class="username">{{rating.username}}</span>
              <div class="star-wrapper">
                <BaseStar :score="rating.score" :size="24"></BaseStar>
              </div>
            </div>
          </div>
          <span class="time">{{rating.rateTime | formatDate}}</span>
          <div class="rating">
            <p class="text">{{rating.text}}</p>
            <div class="icon">
              <i :class="{'icon-thumb_up':rating.rateType === 0,'icon-thumb_down':rating.rateType === 1}"></i>
              <ul class="tags" v-show="ratings && ratings.length">
                <li v-for="(tag,index) in rating.recommend" :key="index" class="tag">
                  {{tag}}
                </li>
              </ul>
              <div class="norating" v-show="!ratings || !ratings.length">暂无评论。</div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
  
</template>

<script>
  import BScroll from 'better-scroll';
  import {formatDate} from 'common/js/date';
  import SelectRatings from 'components/SelectRatings';
  import BaseSpilt from 'components/BaseSpilt';
  import BaseStar from 'components/BaseStar/BaseStar';
  import axios from 'axios';
  const ERR_NO = 0;
  const ALL = 2;
  const debug = process.env.NODE_ENV !== 'production';
  export default {
    data() {
      return {
        ratings: [],
        desc: {
          all: '全部',
          positive: '满意',
          negative: '不满意'
        },
        onlyContent: false,
        selectType: ALL
      };
    },
    methods: {
      select1(type) {
        this.selectType = type;
      },
      toggleContent() {
        this.onlyContent = !this.onlyContent;
      },
      showRating(type, text) {
      if (this.onlyContent && !text) {
        return false;
      }
      if (this.selectType === ALL) {
        return true;
      } else {
        return type === this.selectType;
      }
    }
    },
    components: {
      BaseStar,
      BaseSpilt,
      SelectRatings
    },
    props: {
      seller: {
        type: Object,
        default() {
          return {};
        }
      }
    },
    filters: {
      formatDate(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh-mm-ss');
      }
    },
    created() {
      const url = debug ? '/api/ratings' : 'https://easy-mock.com/mock/5b3ebebe04502462c6feb5bb/ysl2/ratings';
      axios.get(url).then((response) => {
        if (response.data.errno === ERR_NO) {
          this.ratings = response.data.data;
          this.$nextTick(() => {
          this.scroll = new BScroll(this.$refs.ratings, {
            click: true
          });
        });
        }
      });
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../common/stylus/mixin';
  @import '../common/stylus/base';
  .ratings
    position absolute
    top: 174px
    bottom: 0
    left: 0
    width: 100%
    overflow: hidden
    .grade
      display flex
      padding 24px 12px 24px 24px
      .comprehensive
        flex 0 0 127.5px
        border-right 1px solid rgba(7,17,27,.1)
        .score
          color rgb(255,153,0)
          font-size 24px
          text-align center
          line-height 28px
        .text
          padding 6px 0 8px 0
          color rgb(7,17,27)
          font-size 12px
          line-height 26px
          text-align center
        .rankRate
          color rgb(147,153,159)
          text-align center
          font-size 10px
          line-height 10px
      .items
        flex  1
        margin-left 12px
        font-size 12px
        line-height 18px
        .star-wrapper
            display inline-block
            margin 0 10px
            vertical-align top
        .serviceScore
          color rgb(7,17,27)
        .score
          color rgb(255,153,0)
        .deliveryTime
          .time
            margin-left 12px
            color rgb(147,153,159)
    .rating-item
      position relative
      padding 18px 0
      margin 0 18px
      border-1px(rgba(7,17,27,.1))
      .user
        font-size 0
        .avatar
          display inline-block
          border-radius 50%
          vertical-align middle
        .name
          vertical-align middle
          display inline-block
          margin-left 12px
          .username
            color rgb(7,17,27)
            font-size 10px
            line-height 12px
            margin-bottom 1px
          .star-wrapper
            margin-top 2px
      .time
        position absolute
        top 20px
        right 0
        color rgb(147,153,159)
        font-size 10px
        line-height 12px
        font-weight 200
      .rating
        margin-left 40px
        .text
          margin 6px 0 8px 0
          color rgb(7,17,27)
          font-size 12px
          line-height 18px
        .icon
          display flex
          .icon-thumb_up, .icon-thumb_down
            flex 0 1 6px
            display block
            margin-top 2px
            margin-right: 8px
            line-height: 16px
            font-size: 12px
          .icon-thumb_up
            color: rgb(0, 160, 220)
          .icon-thumb_down
            color: rgb(147, 153, 159)
          .tags
            display flex
            flex-wrap wrap
            flex  1
            .tag
              flex  0 0 auto
              margin-right 8px
              margin-bottom 2px
              margin-left 2px
              background-color #fff
              padding 2px 6px
              border 1px solid rgba(7,17,27,.1)
              color rgb(147,153,159)
              font-size 9px
              line-height 16px
              border-radius 1px
</style>



