<template>
  <div class="v-header">
    <div class="content-wrapper">
      <div class="avartar">
        <img :src="seller.avatar" alt="头像" width="64" height="64">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div class="support" v-if="seller.supports">
          <BaseIcon :size="12" :has-bg="false" :type="0"></BaseIcon>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div class="supports-count"  @click="showDetail1">
        <span class="count">5</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper"  @click="showDetail1">
      <span class="bulletin-title"></span><span class="text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%">
    </div>
    <transition name="fade">
      <div class="detail" v-show="showDetail">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <div class="name">{{seller.name}}</div>
            <div class="star-wrapper">
              <BaseStar :score='seller.score' :size="48"></BaseStar>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="name">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li v-for="support in seller.supports" :key="support.type" class="support-item">
                <BaseIcon :size="16" :has-bg="false" :type="support.type"></BaseIcon>
                <span class="text">{{support.description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="name">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close" @click="closeDetail1">
          <i class="icon-close"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BaseIcon from 'components/BaseIcon/BaseIcon';
import BaseStar from 'components/BaseStar/BaseStar';
export default {
  name: 'v-header',
  components: {
    BaseStar,
    BaseIcon
  },
  data() {
    return {
      showDetail: false
    };
  },
  props: {
    seller: {
      type: Object
    }
  },
  methods: {
    showDetail1() {
      this.showDetail = true;
    },
    closeDetail1() {
      this.showDetail = false;
    }
  }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/mixin'
  .fade-enter-active, .fade-leave-active 
    transition: opacity .5s;
  .fade-enter, .fade-leave-to
    opacity 0
  .v-header
    position relative
    overflow hidden
    background-color rgba(7,17,27,0.5)
    color #fff
    .content-wrapper
      position relative
      padding 24px 12px 18px 24px
      font-size 0
      .avartar
        display inline-block
        vertical-align top
        img
          border-radius 2px
      .content
        display inline-block
        margin-left 16px
        .title
          margin 2px 0 8px 0
          .brand
            display inline-block
            width 30px
            height 18px
            bg-image('brand')
            background-size 30px 18px
            background-repeat no-repeat
            vertical-align top
          .name
            margin-left 6px
            font-size 16px
            line-height 18px
            font-weight bold
        .description
          font-size 12px 
          line-height 12px
          font-weight 200
          margin-bottom 10px
        .support
          .text
            font-size 10px
            line-height 12px
      .supports-count
        position absolute
        right 12px
        bottom 14px
        height 24px
        padding 0 8px
        background-color rgba(0,0,0,.2)
        border-radius 14px
        .count
          vertical-align top
          font-size 10px
          line-height 24px
        .icon-keyboard_arrow_right
          font-size 10px
          line-height 24px
    .bulletin-wrapper
      position relative
      height 28px
      padding 0 20px 0 12px
      background-color rgba(7,17,27,.2)
      font-size 10px
      line-height 28px
      overflow hidden
      white-space nowrap
      text-overflow ellipsis
      .bulletin-title
        display inline-block
        width 22px
        height 12px
        margin-top 8px
        margin-right 4px
        background-size 22px 12px
        background-repeat no-repeat
        bg-image('bulletin')
        vertical-align top
      .icon-keyboard_arrow_right
        position absolute
        right 7px
        bottom 8px
        font-size 12px
    .background
      position absolute
      top 0
      left 0
      z-index -1
      width 100%
      height 100%
      filter blur(10px)
    .detail
      position fixed
      top 0
      left 0
      z-index 100
      width 100%
      height 100%
      overflow auto
      background-color rgba(7,17,27,.8)
      // opacity 1
      .detail-wrapper
        width 100%
        min-height 100%
        .detail-main
          width 100%
          margin-top 64px
          padding-bottom 64px
          .name
            font-size 16px
            font-weight 700
            text-align center
          .star-wrapper
            margin-top 18px
            padding 2px 0
            text-align center
          .title
            display flex
            width 80%
            margin 28px auto 0 auto
            .line
              flex  1
              position relative
              top -7px
              border-bottom 1px solid rgba(255,255,255,.2)
            .name
              font-size 14px
              padding 0 12px
              font-weight 700
          .supports
            margin 24px 37.5px 28px 37.5px
            .support-item
              margin-bottom 12px
              padding-left 10px
              font-size 0
              &:last-child 
                margin-bottom 0
              .text
                font-size 12px
                line-height 16px
                font-weight 200
          .bulletin
            width 80%
            margin 24px auto 0 auto
            .content
              padding 0 12px
              font-size 12px
              line-height 24px 
      .detail-close
        position relative
        width 32px
        height 32px
        margin -64px auto 0 auto
        color rgba(255,255,255,.5)
        font-size 32px
        clear both

</style>



