<template>
  <div class="star" :class="starType">
    <span v-for="(itemclass,index) in itemClasses" :key="index"  class="star-item" :class="itemclass"></span>
  </div>
</template>

<script>
export default {
  name: '',
  props: {
    size: {
      type: Number
    },
    score: {
      type: Number
    }
  },
  computed: {
    starType () {
      return 'star-' + this.size;
    },
    itemClasses (score) {
      let result = [];
      let store = Math.floor(this.score * 2) / 2;
      let hasDecimals = (store % 1) !== 0;// 判断是否有小数
      let integer = Math.floor(store);
      for (let i = 0; i < integer; i++) {
        result.push('on');
      };
      if (hasDecimals) {
        result.push('half');
      };
      if (result.length < 5) {
        result.push('off');
      }
      return result;
    }
  }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/mixin.styl'
  .star
    font-size 0
    .star-item
      display inline-block
      background-repeat no-repeat
    &.star-48
      .star-item
        width 20px
        height 20px
        margin-right 22px
        background-size 20px 20px
        &:last-child
          margin-right 0
        &.on
          bg-image('star48_on')
        &.half
          bg-image('star48_half')
        &.off
          bg-image('star48_off')
    &.star-36
      .star-item
        width 15px
        height 15px
        margin-right 6px
        background-size 15px 15px
        &:last-child
          margin-right 0
        &.on
          bg-image('star36_on')
        &.half
          bg-image('star36_half')
        &.off
          bg-image('star36_off')
    &.star-24
      .star-item
        width 10px
        height 10px
        margin-right 3px
        background-size 10px 10px
        &:last-child
          margin-right 0
        &.on
          bg-image('star24_on')
        &.half
          bg-image('star24_half')
        &.off
          bg-image('star24_off')
</style>



