<template>
  <div>
    <div class="shopcar">
      <div class="content">
        <div class="content-left" @click="toggleShowShop">
          <div class="logo-wrapper">
            <div class="logo" :class="{'hightLight':totalCount>0}">
              <i class="icon-shopping_cart" :class="{'hightLight':totalCount>0}"></i>
            </div>
            <div class="num" v-show="totalCount>0">{{totalCount}}</div>
          </div>
          <div class="price" :class="{'hightLight':totalPrice>0}">￥{{totalPrice}}元</div>
          <div class="desc">需要配送费{{deliveryPrice}}元</div>
        </div>
        <div class="content-right" :class="enough" @click="pay">{{payDesc}}</div>
      </div>
      <transition name="fold">
        <div class="showcar-list" v-show="listShow">
          <div class="showcar-header border-1px">
            <span class="text">购物车</span>
            <span class="clear" @click="clear">清空</span>
          </div>
          <div class="showcar-content" ref="listContent">
            <ul>
              <li v-for="(food,index) in selectFoods" :key="index" class="food-list border-1px">
                <div class="name">{{food.name}}</div>
                <div class="cartcontrol-wrapper">
                  <ControlCar :food="food"></ControlCar>
                </div>
                <div class="price">￥{{food.price}}</div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade">
      <div class="listmask" v-show="listShow" @click="hidecar"></div>
    </transition>
  </div>
</template>

<script>
  import BScroll from 'better-scroll';
  import ControlCar from 'components/PageGoodsShopCarControlCar';
  export default {
    props: {
      selectFoods: {
        type: Array,
        default () {
          return [];
        }
      },
      deliveryPrice: {
        type: Number,
        default: 0
      },
      minPrice: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        shopGoods: this.selectFoods,
        fold: true
      };
    },
    components: {
      ControlCar
    },
    computed: {
      totalPrice() {
        let total = 0;
        this.selectFoods.forEach(food => {
          total += food.price * food.count;
        });
        return total;
      },
      totalCount() {
        let count = 0;
        this.selectFoods.forEach(food => {
          count += food.count;
        });
        return count;
      },
      payDesc() {
        if (this.totalPrice === 0) {
          return `￥${this.minPrice}起送`;
        } else if (this.totalPrice >= this.minPrice) {
          return '去结算';
        } else if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice;
          return `还差${diff}起送`;
        }
      },
      enough() {
        return this.totalPrice >= this.minPrice ? 'enough' : 'not-enough';
      },
      listShow() {
        if (!this.totalCount) {
          // eslint-disable-next-line
          this.fold = true;
          return false;
        }
        let show = !this.fold;
         if (show) {
          this.$nextTick(() => {
            if (!this.scroll) {
               // eslint-disable-next-line
              this.scroll = new BScroll(this.$refs.listContent, {
                click: true
              });
            } else {
              this.scroll.refresh();
            }
          });
        }
        return show;
      }
    },
    methods: {
      clear() {
        this.selectFoods.forEach(food => {
          food.count = 0;
        });
    },
      toggleShowShop() {
        if (!this.totalCount) {
          return;
        }
        this.fold = !this.fold;
      },
      hidecar() {
        this.fold = true;
      },
      pay() {
        if (this.totalPrice < this.minPrice) {
          // eslint-disable-next-line
          return;
        } else {
          window.alert(`支付${this.totalPrice}元`);
        }
      }
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../common/stylus/mixin';
  .shopcar
    position fixed
    left 0
    bottom 0
    z-index 90
    width 100%
    height 48px
    .content
      position relative
      z-index 100
      display flex
      width 100%
      height 48px
      background-color #141d27
      .content-left
        flex  1
        height 100%
        .logo-wrapper
          display inline-block
          position relative
          vertical-align top
          top -10px
          z-index 100
          width 56px
          height 56px
          margin 0 12px
          padding 6px
          box-sizing border-box
          border-radius 50%
          background-color #141d27;
          .logo
            width 100%
            height 100%
            background-color #2b343c
            text-align center
            border-radius 50%
            &.hightLight
              background-color #00a0dc
            .icon-shopping_cart
              font-size 24px
              line-height 44px
              color: #80858a
              &.hightLight
                color #fff
          .num
            position absolute
            top 0
            right 0
            width 24px
            height 16px
            background-color #f01414
            color #fff
            text-align center
            font-size 9px
            line-height 16px
            border-radius 16px
            font-weight 700
            box-shadow 0 4px 8px rgba(0,0,0,.4)
        .price
          display inline-block
          box-sizing border-box
          margin-top 12px
          padding-right 12px
          border-right 1px solid rgba(255,255,255,.1)
          color rgba(255,255,255,0.4)
          vertical-align top
          font-size 16px
          line-height 24px
          font-weight 700
          &.hightLight
            color #fff
        .desc
          display inline-block
          margin 12px 0 0 12px
          vertical-align top
          color rgba(255,255,255,.4)
          font-size 10px
          line-height 24px
      .content-right
        flex 0 0 105px
        width 105px
        height 100%
        font-size 12px
        line-height 48px
        font-weight 700
        text-align center
        &.enough
          background-color #00b43c
          color #fff
        &.not-enough
          background-color #2b333b
          color rgba(255,255,255,.4)
    .showcar-list
      position absolute
      left 0
      top 0
      z-index -1
      width 100%
      max-height 258px
      transform: translate3d(0, -100%, 0)
      &.fold-enter-active, &.fold-leave-active
        transition: all 0.5s
      &.fold-enter, &.fold-leave-active
        transform: translate3d(0, 0, 0)
      .showcar-header
        height 40px
        padding 0 18px
        background-color #f3f5f7
        border-1px(rgba(7,17,27,0.1))
        span 
          color rgb(7,17,27)
          font-weight 200
          font-size 14px  
          line-height 40px
          &.text
            float left 
          &.clear
            float right
            color #00a0dc
      .showcar-content
        max-height 217px
        padding 0 18px
        background-color #fff
        overflow hidden
        .food-list
          height 48px
          border-1px(rgba(7,17,27,0.1))
          .name
            float left
            height 24px
            font-size 14px
            line-height 48px
          .price
            float right
            color rgb(245,20,20)
            font-size 14px
            font-weight 700
            line-height 48px
            margin-right 12px

          .cartcontrol-wrapper
            float right
            height 24px
            margin-top 6px
  .listmask
    position fixed
    top 0
    left 0
    width 100%
    height 100%
    z-index: 40
    backdrop-filter: blur(10px)
    opacity: 1
    background: rgba(7, 17, 27, 0.6)
    &.fade-enter-active, &.fade-leave-active
      transition: all 0.5s
    &.fade-enter, &.fade-leave-active
      opacity: 0
      background: rgba(7, 17, 27, 0)
</style>



