<template>
  <div class="cartcontrol">
    <transition name="move">
      <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart">
        <i class="icon-remove_circle_outline inner"></i>
      </div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <div class="cart-add icon-add_circle" @click.stop.prevent="addCart">
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      food: {
        type: Object,
        default () {
          return {};
        }
      }
    },
    methods: {
        addCart() {
          if (!this.food.count) {
            this.$set(this.food, 'count', 1);
            return 1;
          } else {
            this.food.count++;
          }
        },
        decreaseCart() {
          if (this.food.count) {
            this.food.count--;
          };
        }
      }
    
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .cartcontrol
    font-size 0
    .cart-decrease
      display inline-block
      padding 6px
      opacity: 1
      transform: translate3d(0, 0, 0)
      .inner
        display inline-block
        color rgb(0,160,220) 
        font-size 24px
        line-height 24px
        transition: all 0.3s linear
        transform: rotate(0)
      &.move-enter-active, &.move-leave-active
        transition: all 0.3s linear
      &.move-enter, &.move-leave-active
        opacity: 0
        transform: translate3d(24px, 0, 0)
        .inner
          transform: rotate(180deg)
    .cart-count
      display inline-block
      width 12px
      padding-top 6px
      vertical-align top
      color rgb(147,153,159)
      text-align  center
      font-size 10px
      line-height 24px
    .cart-add
      display inline-block
      padding 6px
      color rgb(0,160,220) 
      font-size 24px
      line-height 24px
</style>



