  <template>
  <transition name="move">
    <div class="food" v-show="showFlag" ref="food">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.image" alt="">
          <div class="back-button" @click="hide">
            <i class="icon-arrow_lift"></i>
          </div>
        </div>
        <div class="content">
          <h1 class="title">{{food.name}}</h1>
          <div class="detail">
            <span class="count">月售{{food.sellCount}}份</span>
            <span class="rating">好评率{{food.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">￥{{food.price}}</span>
            <span v-show="food.oldPrice" class="old">{{food.oldPrice}}</span>
          </div>
          <div class="cartcontrol-wrapper" v-show="food.count > 0">
            <ControlCar :food="food"></ControlCar>
          </div>
          <transition name="fade">
            <div class="buy" v-show="food.count === 0 || !food.count" @click="buy">
            加入购物车
            </div>
          </transition>
        </div>
        <BaseSpilt v-show="food.info"></BaseSpilt>
        <div class="food-info" v-show="food.info">
          <h1 class="title">商品信息</h1>
          <p class="text">{{food.info}}</p>
        </div>
        <BaseSpilt></BaseSpilt>
        <div class="rating">
          <h1 class="title">商品评价</h1>
          <SelectRatings :ratings="food.ratings" :desc="desc" :onlyContent="onlyContent" :selectType="selectType" @custom="select1" @toggle="toggleContent"></SelectRatings>
          <div class="rating-wrapper">
            <ul v-show="food.ratings && food.ratings.length">
              <li class="rating-item border-1px" v-for="(rating,index) in food.ratings" :key="index" v-show="showRating(rating.rateType, rating.text)">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img :src="rating.avatar" class="avatar" width="12" height="12">
                </div>
                <div class="time">{{rating.rateTime | _formatDate}}</div>
                <p class="text">
                  <i :class="{'icon-thumb_up':rating.rateType === 0,'icon-thumb_down':rating.rateType === 1}"></i>
                  {{rating.text}}
                </p>
              </li>
            </ul>
            <div class="norating" v-show="!food.ratings || !food.ratings.length">暂无评论。</div>
          </div>
        </div>
      </div>
    </div>
  </transition>
 
</template>

<script>
  import BScroll from 'better-scroll';
  import SelectRatings from 'components/SelectRatings';
  import BaseSpilt from 'components/BaseSpilt';
  import ControlCar from 'components/PageGoodsShopCarControlCar';
  import {formatDate} from 'common/js/date.js';
  const ALL = 2;
  export default {
    props: {
     food: {
       type: Object,
       default() {
         return {};
       }
      }
    },
    components: {
      ControlCar,
      SelectRatings,
      BaseSpilt
    },
    data() {
      return {
        showFlag: false,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        },
        onlyContent: false,
        selectType: ALL
      };
    },
    methods: {
      show() {
      this.showFlag = true;
      this.selectType = ALL;
      this.onlyContent = false;
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
      },
      hide() {
        this.showFlag = false;
      },
      buy() {
        this.$set(this.food, 'count', 1);
      },
      showRating(type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      },
      select1(type) {
        this.selectType = type;
      },
      toggleContent() {
        this.onlyContent = !this.onlyContent;
      }
    },
    filters: {
      _formatDate(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh-mm-ss');
      }
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../common/stylus/mixin'
  .food
    position fixed
    top 0
    left 0
    bottom 48px
    width 100%
    background-color #fff
    transform: translate3d(0, 0, 0)
    &.move-enter-active, &.move-leave-active
      transition: all 0.2s linear
    &.move-enter, &.move-leave-active
      transform: translate3d(100%, 0, 0)
    .image-header
      position relative
      top 0
      left 0
      width 100%
      height 0%
      padding-top 100%
      img 
        position absolute
        top 0
        left 0
        width 100%
        height 100%
      .back-button
        position absolute
        top 10px
        left 5px
        .icon-arrow_lift
          display inline-block
          padding 10px
          color white
          font-size 20px
    .content
      position relative
      padding 18px
      .title
        margin-bottom 8px
        color rgb(7,17,27)
        font-size 14px
        font-weight 700
        line-height 14px
      .detail
        color rgb(147,158,159)
        font-size 10px
        line-height 10px
        .count
          margin-right 12px
      .price
        display inline-block
        margin-top 12px
        margin-left 18px
        .now
          margin-right 12px
          color rgb(245,20,20)
          font-size 14px
          line-height 24px
          font-weight 700
        .old
          color rgb(147,153,159)
          font-size 12px
          line-height 24px
          font-weight 700
          text-decoration line-through
      .buy
        position absolute
        right 18px
        bottom 18px
        height 24px
        padding 0 12px
        background-color rgb(0,160,220)
        color white
        font-size 10px
        line-height 24px
        border-radius 12px
        box-sizing border-box
        opacity 1
        &.fade-enter-active, &.fade-leave-active
          transition: all 0.2s
        &.fade-enter, &.fade-leave-active
          opacity: 0
          z-index: -1
      .cartcontrol-wrapper
        position absolute
        right 18px
        bottom 12px
    .food-info
      padding 18px
      .title
        margin-bottom 6px
        color rgb(7,17,27)
        font-size 14px
        line-height 14px
      .text
        padding 0 8px
        color rgb(77,85,93)
        font-size 12px
        line-height 24px
        font-weight 200
    .rating
      padding-top 18px
      .title
        margin-left 18px
        color rgb(7,17,27)
        font-size 14px
        line-height 14px
      .rating-wrapper
        padding 0 18px
        .rating-item
          position relative
          padding 16px 0
          border-1px(rgba(7,17,27,.1))
          .user
            position absolute
            display inline-block
            top 16px
            right 0
            vertical-align top
            .name
              margin-right 6px
              color rgb(147,153,159)
              vertical-align top
              font-size 10px
              line-height 14px
            .avatar
              vertical-align top
              border-radius 50%
          .text
            line-height: 16px
            font-size: 12px
            color: rgb(7, 17, 27)
            .icon-thumb_up, .icon-thumb_down
              margin-right: 4px
              line-height: 16px
              font-size: 12px
            .icon-thumb_up
              color: rgb(0, 160, 220)
            .icon-thumb_down
              color: rgb(147, 153, 159)
          .time
            margin-bottom 2px
            color rgb(147,153,159)
            font-size 10px
            line-height 12px
        .norating
          padding: 16px 0
          font-size: 12px
          color: rgb(147, 153, 159)

</style>



