<template>
  <i class="icon" :class="itemClasses"></i>
</template>

<script>
  export default {
    props: {
      size: {
        type: Number,
        default: 12
      },
      type: {
        type: Number,
        detault: 0
      },
      hasBg: {
        type: Boolean,
        detault: true
      }
    },
    // data() {
    //   return {
    //     classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    //   };
    // },
    computed: {
      itemClasses(size, type, hasBg) {
        let result = '';
        if (this.size === 12 && this.hasBg) {
          switch (this.type) {
            case 0:
              return result = 'size12 decrease bg';
              break;
            case 1:
              return result = 'size12 discount bg';
              break;
            case 2:
              return result = 'size12 special bg';
              break;
            case 3:
              return result = 'size12 invoice bg';
              break;
            case 4:
              return result = 'size12 guarantee bg';
              break;
          }
        } else if (this.size === 12 && !this.hasBg) {
          switch (this.type) {
            case 0:
              return result = 'size12 decrease nobg';
              break;
            case 1:
              return result = 'size12 discount nobg';
              break;
            case 2:
              return result = 'size12 special nobg';
              break;
            case 3:
              return result = 'size12 invoice nobg';
              break;
            case 4:
              return result = 'size12 guarantee nobg';
              break;
          }
        } else if (this.size === 16 && this.hasBg) {
          switch (this.type) {
            case 0:
              return result = 'size16 decrease bg';
              break;
            case 1:
              return result = 'size16 discount bg';
              break;
            case 2:
              return result = 'size16 special bg';
              break;
            case 3:
              return result = 'size16 invoice bg';
              break;
            case 4:
              return result = 'size16 guarantee bg';
              break;
          }
        } else {
          switch (this.type) {
            case 0:
              return result = 'size16 decrease nobg';
              break;
            case 1:
              return result = 'size16 discount nobg';
              break;
            case 2:
              return result = 'size16 special nobg';
              break;
            case 3:
              return result = 'size16 invoice nobg';
              break;
            case 4:
              return result = 'size16 guarantee nobg';
              break;
          }
        }
      }
      // test() {
      //   if (this.size === 16 && hasBg) {
      //     this
      //   }
      // }
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/mixin'
  .icon
    display inline-block
    &.size12
      width 12px
      height 12px
      margin-right 2px
      background-size 12px 12px
      background-repeat no-repeat
      vertical-align top
      &.decrease
        &.nobg
          bg-image('decrease_1')
        &.bg
          bg-image('decrease_3')
      &.discount
        &.nobg
          bg-image('discount_1')
        &.bg
           bg-image('discount_3')
      &.guarantee
        &.nobg
          bg-image('guarantee_1')
         &.bg
          bg-image('guarantee_3')
      &.invoice
        &.nobg
          bg-image('invoice_1')
         &.bg
          bg-image('invoice_3')
      &.special
        &.nobg
          bg-image('special_1')
        &.bg
           bg-image('special_3')
    &.size16
      width 16px
      height 16px
      margin-right 6px
      background-size 16px 16px
      background-repeat no-repeat
      vertical-align top
      &.decrease
        &.nobg
          bg-image('decrease_2')
        &.bg
          bg-image('decrease_4')
      &.discount
        &.nobg
          bg-image('discount_2')
        &.bg
           bg-image('discount_4')
      &.guarantee
        &.nobg
          bg-image('guarantee_2')
         &.bg
          bg-image('guarantee_4')
      &.invoice
        &.nobg
          bg-image('invoice_2')
         &.bg
          bg-image('invoice_4')
      &.special
        &.nobg
          bg-image('special_1')
        &.bg
           bg-image('special_4')
</style>



