<template>
  <div class="ratingselect">
    <div class="selectType border-1px">
      <span class="block position" :class="{'active':selectType === 2}" @click="custom(2)">{{desc.all}}
        <span class="count">{{ratings.length}}</span>
      </span>
      <span class="block position" :class="{'active':selectType === 0}" @click="custom(0)">{{desc.positive}}
        <span class="count">{{positive.length}}</span>
      </span>
      <span class="block negative" :class="{'active':selectType === 1}" @click="custom(1)">{{desc.negative}}
        <span class="count">{{negative.length}}</span>
      </span>
    </div>
    <div class="switch" :class="{'on':onlyContent===true}" @click="toggleSwitch">
      <i class="icon-check_circle"></i>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script>
  const POSITIVE = 0;
  const NAGETIVE = 1;
  const ALL = 2;
  export default {
    props: {
      ratings: {
        type: Array,
        default() {
          return [];
        }
      },
      selectType: {
        type: Number,
        default: ALL
      },
      onlyContent: {
        type: Boolean,
        default: false
      },
      desc: {
        type: Object,
        default() {
          return {
            all: '全部',
            positive: '满意',
            negative: '不满意'
          };
        }
      }
    },
    computed: {
      positive() {
        return this.ratings.filter((rating) => {
          return rating.rateType === POSITIVE;
        });
      },
      negative() {
        return this.ratings.filter((rating) => {
          return rating.rateType === NAGETIVE;
        });
      }
    },
    methods: {
      showRating(type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      },
      custom(type) {
        this.$emit('custom', type);
      },
      toggleSwitch() {
        this.$emit('toggle');
      }
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../common/stylus/mixin'
  .ratingselect
    .selectType
      position relative
      margin 0 18px
      padding 18px 0
      border-1px(rgba(7,17,27,.1))
      .block
        display inline-block
        margin-right 8px
        padding 8px 12px
        font-size 12px
        line-height 16px
        border-radius 1px
        &.position
          background-color rgba(0,160,220,.2)
          &.active
            background-color #00a0dc
            color white
        &.negative
          background-color rgba(77,85,93,0.2)
          &.active
            background-color rgb(77,85,93)
            color white
    .switch
      padding 12px 18px
      border-bottom 1px solid rgba(7,17,27,.1)
      font-size 0
      line-height 24px
      &.on
        .icon-check_circle
          color #00c850
      .icon-check_circle
        display inline-block
        margin-right 4px
        color #93999f
        vertical-align top
        font-size 24px
      .text
        display inline-block
        color rgb(147,153,159)
        vertical-align top
        font-size 12px
</style>



