<template>
  <div>
    <div class="goods">
      <div class="menu-wrapper" ref="menu">
        <ul>
          <li class="list-item" v-for="(item,index) in goods" :key="index" :class="{isCurrent:currentIndex === index}" @click="followGoods(index)">
            <span class="text border-1px">
              <BaseIcon :size="12" :has-bg="true" :type="item.type"></BaseIcon>{{item.name}}
            </span>
          </li>
        </ul>
      </div>
      <div class="goods-wrapper" ref="goods">
        <ul>
          <li class="food-list" v-for="(item,index) in goods" :key="index" ref="foodItem">
            <h1 class="title">{{item.name}}</h1>
            <ul>
              <li class="food-item border-1px" v-for="(food,index) in item.foods" :key="index" @click="_selectedFood(food)">
                <div class="icon">
                  <img :src="food.icon" width="57" height="57">
                </div>
                <div class="content">
                  <h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span>
                  <span>好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{food.price}}</span>
                    <span v-show="food.oldPrice" class="old">{{food.oldPrice}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <ControlCar :food="food"></ControlCar>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <ShopCar :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice" :selectFoods="selectFoods"></ShopCar>
    </div>
    <PageGoodsFood :food="selectedFood" ref="food"></PageGoodsFood>
  </div>
</template>

<script>
  import BaseIcon from 'components/BaseIcon/BaseIcon';
  import PageGoodsFood from 'components/PageGoodsFood';
  import BScroll from 'better-scroll';
  import axios from 'axios';
  import ShopCar from 'components/PageGoodsShopCar';
  import ControlCar from 'components/PageGoodsShopCarControlCar';
  const debug = process.env.NODE_ENV !== 'production';
  const ERR_NO = 0;
  export default {
    components: {
      ShopCar,
      ControlCar,
      PageGoodsFood,
      BaseIcon
    },
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        goods: [],
        listHeight: [],
        scrollY: 0,
        selectedFood: {}
      };
    },
    computed: {
      currentIndex () {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i;
          };
        }
        return 0;
      },
      selectFoods() {
        let foods = [];
        this.goods.forEach(good => {
          good.foods.forEach(food => {
            if (food.count) {
              foods.push(food);
            }
          });
        });
        return foods;
      }
    },
    created() {
      const url = debug ? '/api/goods' : 'https://easy-mock.com/mock/5b3ebebe04502462c6feb5bb/ysl2/goods';
      axios.get(url).then(response => {
        if (response.data.errno === ERR_NO) {
          this.goods = response.data.data;
          this.$nextTick(() => {
            this._initScroll();
            this.calculateHight();
          });
        } 
      });
    },
    methods: {
      _initScroll() {
        this.menuScroll = new BScroll(this.$refs.menu, {
          click: true
        });
        this.goodsScroll = new BScroll(this.$refs.goods, {
          click: true,
          probeType: 3
        });
        this.goodsScroll.on('scroll', (pos) => {
          if (pos.y <= 0) {
            this.scrollY = Math.abs(Math.round(pos.y));
          };
        });
      },
      calculateHight() {
        let liList = this.$refs.foodItem;
        let height = 0;
        this.listHeight.push(height);
        for (let i = 0; i < liList.length; i++) {
          let itemHeight = liList[i].clientHeight;
          height += itemHeight;
          this.listHeight.push(height);
        };
      },
      followGoods(index) {
        this.goodsScroll.scrollToElement(this.$refs.foodItem[index], 300, 0, 0);
      },
      _selectedFood(food) {
        this.selectedFood = food;
        this.$refs.food.show();
      }
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../common/stylus/mixin';
  @import '../common/stylus/base';
  .goods
    display flex
    position absolute
    top 174px
    bottom 46px
    width 100%
    overflow hidden
    .menu-wrapper
      flex 0 0 80px
      width 80px
      background-color #f3f5f7
      .list-item
        display table
        width 56px
        height 54px
        padding 0 12px
        color #000
        &.isCurrent
          position relative
          z-index 10
          margin-top -1px
          background-color #fff
          font-weight bold
          .text
            border-none()
            font-weight 700
        .text
          display table-cell
          vertical-align middle
          border-1px(rgba(7,17,27,.1))
          font-size 12px
          line-height 14px
          font-weight 400
    .goods-wrapper
      flex 1
      .title
        height 26px
        padding-left 14px
        border-left 2px solid #d9dde1
        background-color #f3f5f7
        color #93999f
        font-size 12px
        line-height 26px
      .food-item
        display flex
        margin 18px
        padding-bottom 18px
        border-1px(rgba(7,17,27,.1))
        &:last-child
          margin-bottom 0
          border-none()
        .icon
          flex 0 0 57px
          margin-right 10px
        .content
          flex 1
          .name
            height 14px
            margin 2px 0 4px 0
            color rgb(7,17,27)
            font-size 14px
            line-height 14px
            font-weight 400
          .desc, .extra
            color rgb(147,123,159)
            font-size 10px
            line-height 10px
          .desc
            margin 8px 0
            line-height 14px
          .extra
            .count
              margin-right 12px
          .price
            display inline-block
            margin-top 5px
            .now
              margin-right 12px
              color rgb(245,20,20)
              font-size 14px
              line-height 24px
              font-weight 700
            .old
              color rgb(147,153,159)
              font-size 12px
              line-height 24px
              font-weight 700
              text-decoration line-through
          .cartcontrol-wrapper
            position absolute
            right 0
            bottom 12px
</style>



