<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="header">
        <div class="header-top border-1px">
          <div class="left">
            <h2 class="title">{{seller.name}}</h2>
            <div class="star-wrapper">
              <BaseStar class="star" :score="seller.score" :size="36"></BaseStar>
              <span class="ratings num">({{seller.ratingCount}})</span>
              <span class="sellCount num">月售{{seller.sellCount}}单</span>
            </div>
          </div>
          <div class="right">
            <div class="icon">
              <i class="icon-favorite love" :class="{'collect': love}" @click="toggle"></i>
            </div>
            <span class="text">{{loveText}}</span>
          </div>
        </div>
        <div class="header-bottom">
          <div class="minPrice item">
            <div class="title">起送价</div>
            <span class="common">{{seller.minPrice}}</span>元
          </div>
          <div class="deliveryPrice item">
            <div class="title">配送价</div>
            <span class="common">{{seller.deliveryPrice}}</span>元
          </div>
          <div class="time item">
            <div class="title">平均配送时间</div>
            <span class="common">{{seller.deliveryTime}}</span>分钟
          </div>
        </div>
      </div>
      <BaseSpilt></BaseSpilt>
      <div class="seller-bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="bulletin border-1px">{{seller.bulletin}}</div>
        <ul>
          <li class="list-item border-1px" v-for="(support,index) in seller.supports" :key="index">
            <BaseIcon :size="16" :type="support.type" :hasBg="true"></BaseIcon>
            <span class="text">{{support.description}}</span>
          </li>
        </ul>
      </div>
      <BaseSpilt></BaseSpilt>
      <div class="seller-pics">
        <h1 class="title">商家实景</h1>
        <div class="pics-wrapper" ref="picsWrapper">
          <ul class="items" ref="items">
            <li v-for="(pic,index) in seller.pics" :key="index" class="pic-item">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <BaseSpilt></BaseSpilt>
      <div class="seller-info">
        <h1 class="title border-1px">商家信息</h1>
        <ul>
          <li v-for="(info,index) in seller.infos" class="info-item border-1px" :key="index">{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import {saveToLocal, loadFromLocal} from 'common/js/store';
  import BScroll from 'better-scroll';
  import BaseIcon from 'components/BaseIcon/BaseIcon';
  import BaseSpilt from 'components/BaseSpilt';
  import BaseStar from 'components/BaseStar/BaseStar';
  export default {
    components: {
      BaseStar,
      BaseSpilt,
      BaseIcon
    },
    props: {
      seller: {
        type: Object,
        default() {
          return {};
        }
      }
    },
    data() {
      return {
        love: (() => {
          return loadFromLocal(this.seller.id, 'love', false);
        })()
      };
    },
    computed: {
      loveText() {
        return this.love ? '已收藏' : '收藏';
      }
    },
    watch: {
      'seller'() {
        this.$nextTick(() => {
          this._initPics();
          this._initScroll();
        });
      }
    },
    methods: {
      toggle() {
        this.love = !this.love;
        saveToLocal(this.seller.id, 'love', this.love);
      },
      _initScroll() {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.seller, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      },
      _initPics() {
        let width = 120;
        let margin = 6;
        let ulWidth = (width + margin) * 4 - margin;
        this.$refs.items.style.width = ulWidth + 'px';

        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$refs.picsWrapper, {
              scrollX: true,
              eventPassthrough: 'vertical'
            });
          } else {
            this.picScroll.refresh();
          }
        });
      }
    },
    mounted() {
      this.$nextTick(() => {
        this._initPics();
        this._initScroll();
      });
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '../common/stylus/base';
  @import '../common/stylus/mixin';
  .seller
    position absolute
    top: 174px
    bottom: 0
    left: 0
    width: 100%
    overflow: hidden
    .seller-content
      .header
        padding 18px
        .header-top
          display flex
          justify-content space-between
          width 100%
          padding-bottom 18px
          border-1px(rgba(7,17,27,.1))
          .left
            flex 1
            font-size 0
            .title
              color rgb(7,17,27)
              font-size 14px
              line-height 14px
            .star-wrapper
              margin-top 8px
              .star
                display inline-block
                vertical-align top
              .num
                color rgb(77,85,93)
                font-size 10px
                line-height 18px
                vertical-align top
                &.ratings
                  margin-right 12px
                  margin-left 8px
          .right
            flex 0 0 36px
            text-align center
            .icon
              color #d4d6d9
              font-size 24px
              line-height 24px
              margin-bottom 2px
              .love
                &.collect
                  color rgb(240,20,20)
            .text
              color rgb(77,85,93)
              font-size 10px
              line-height 10px
        .header-bottom
          display flex
          padding 18px 18px 0px 18px
          .deliveryPrice
            border-right 1px solid rgba(7,17,27,.1)
            border-left 1px solid rgba(7,17,27,.1)
          .item
            flex 1
            font-size 10px
            text-align center
            .title
              margin-bottom 4px
              color rgb(147,153,159)
              font-size 10px
              line-height 10px
            .common
              color rgb(7,17,27)
              font-size 24px
              line-height 24px
              font-weight 200
      .seller-bulletin
        padding 18px
        .title
          color rgb(7,17,27)
          font-size 14px
          line-height 14px
        .bulletin
          padding 8px 12px 16px
          border-1px(rgba(7,17,27,.1))
          color rgb(240,20,20)
          font-size 12px
          line-height 24px
          font-weight 200
        .list-item
          padding 16px 12px
          border-1px(rgba(7,17,27,.1))
          color rgb(7,17,27)
          font-size 12px
          line-height 16px
      .seller-pics
        padding 18px
        .title
          color rgb(7,17,27)
          font-size 14px
          line-height 14px
          margin-bottom 12px
        .pics-wrapper
          width 100%
          overflow hidden
          white-space nowrap
          .items
            font-size 0
            .pic-item
              display inline-block
              margin-right 6px
      .seller-info
        padding 18px 18px 0px 18px
        .title
          padding-bottom 12px
          color rgb(7,17,27)
          font-size 14px
          line-height 14px
          border-1px(rgba(7,17,27,.1))
        .info-item
          padding 16px 12px
          border-1px(rgba(7,17,27,.1))
          color rgb(7,17,27)
          font-size 12px
          line-height 16px
          &:last-child
            border-none()
</style>



