import Vue from 'vue';
import Router from 'vue-router';
import PageGoods from 'components/PageGoods';
import PageRatings from 'components/PageRatings';
import PageSeller from 'components/PageSeller';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/goods'
    },
    {
      path: '/goods',
      component: PageGoods
    },
    {
      path: '/ratings',
      component: PageRatings
    },
    {
      path: '/seller',
      component: PageSeller
    }
  ]
});



